html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Open Sans", sans-serif, Arial;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: rgb(25, 25, 25);
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.container {
  width: 100%;
  margin: 0;
  display: flex;
  height: 100vh;
}

img {
  max-width: 100%;
  display: block;
}

:root {
  --login-bg:#e9e7ec;
  --image-bg: #eadbff ;
  --azul-bg:#1EAEFF;
  --azul-claro: #9cd2f3;
  --gris-claro: #D9D9D9;
  --negro: #000;
  --blanco: white;
  --gris-hr: #aca9a9;
}

body {
  height: 80vh;
  font-family: "Poppins", sans-serif;
}

/*-----Index-----*/
.container {
  width: 60em;
  margin: 1.5em auto;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

.info-container {
  height: 36em;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 0.5rem;
  background-color: var(--login-bg);
  border-radius: 10px 0px 0px 10px;
}

.image-container {
  /*height: 576px;*/
  background: url("https://thelogisticsworld.com/wp-content/uploads/2020/10/0000069281-scaled.jpg") no-repeat;
  background-size: cover;
  padding: 2rem;
  width: 50%;
  box-sizing: border-box;
  background-position: 90% 40%;
  border-radius: 0px 10px 10px 0px;
}

.title {
  margin-top: 6rem;
  text-transform: capitalize;
  font-size: 2.25rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--negro);
}

.select-social-login-element {
  margin-top: 4rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.select-social-login-element a {
  color: #000;
  text-decoration: none;
}

.select-social-login-element {
  cursor: pointer;
  margin-top: 4rem;
  width: 20rem;
  height: 3.75rem;
  font-size: 1.2rem;
  font-weight: lighter;
  letter-spacing: 0.5px;
  border-radius: 5px;
  border: 1px solid var(--azul-bg);
  display: flex;
  justify-content: center;
  align-items: center;
}

.select-social-login-element img {
  width: 1.875rem;
  height: 1.875rem;
  position: relative;
  top: 0;
  left: -0.625rem;
}

.select-social-login-element:hover {
  background-color: #1EAEFF;
  color: var(--blanco);
  transition: all ease 0.3s;
}

.select-span {
  padding: 1.14rem 7.6rem;
}

.select-span2 {
  padding: 1.11rem 6.01rem;
  margin-right: 0.1rem;
}

@media screen and (max-width: 1000px) {
  .container {
    width: 70%;
  }
  .info-container {
    width: 100%;
    border-radius: 5px;
  }
  .image-container {
    display: none;
  }
}
@media screen and (max-width: 650px) {
  .container {
    width: 90%;
  }
}
@media screen and (max-width: 500px) {
  .container {
    height: 125%;
  }
}
/*----Login----*/
.return {
  margin-left: -0.2rem;
  margin-bottom: 1rem;
}

.return1 {
  padding: 0.2rem 0.3rem;
}

.return1:hover {
  color: var(--azul-bg);
}

.return1 a {
  color: var(--negro);
  text-decoration: none;
}

.return1 a:hover {
  color: var(--azul-bg);
}

.inputs-container {
  margin: 1rem;
  height: 100%;
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.info-container a {
  text-decoration: none;
}

.input, .btn {
  width: 90%;
  height: 3.125rem;
  font-size: 1em;
}

.input {
  padding-left: 20px;
  border: none;
  border-radius: 5px;
  font-weight: 300;
  letter-spacing: 1px;
  box-sizing: border-box;
  background-color: var(--blanco);
}

.input:hover {
  border: 1px solid var(--negro);
}

.input-select {
  margin-top: 1em;
}

.btn {
  width: 80%;
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--blanco);
  border: none;
  border-radius: 5px;
  background-color: var(--azul-bg);
  cursor: pointer;
}

.btn:hover {
  background-color: var(--azul-claro);
  transition: all ease 0.3s;
  color: var(--negro);
}

.btn-container {
  width: 90%;
}

.radio-button-container {
  display: flex;
  width: 20%;
}

.radio-button-container label {
  margin-left: 10px;
}

.radio-button-container input {
  transform: scale(1.5);
}

.btn-seleccion {
  width: 80%;
  padding: 1.375rem 0.75rem;
  border: 1px solid #007bff;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out;
  background-color: var(--login-bg);
  margin: 2.5em 0 0 0;
}

.btn-seleccion:hover {
  background-color: var(--azul-bg);
  transition: all ease 0.3s;
}

.btn_border_opacity {
  background-color: var(--login-bg);
  border-color: transparent;
}

.span {
  padding: 0.5rem;
  color: var(--negro);
  font-weight: 300;
  cursor: pointer;
}

.span:hover {
  color: var(--azul-bg);
}

.hr1 {
  width: 80%;
  height: 0.1px;
  background-color: var(--gris-hr);
}

.pointer_button {
  cursor: pointer;
}

/*-----Register-----*/
.container-register {
  height: 92em;
  width: 60em;
  margin: 3rem auto;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

.info-container-register {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 0.5rem;
  background-color: var(--login-bg);
  margin-bottom: 4rem;
  border-radius: 10px;
}

.title-register {
  margin-top: 2rem;
  text-transform: capitalize;
  font-size: 2.25rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--negro);
}

.inputs-container-register {
  margin-bottom: 3rem;
  height: 85rem;
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.register {
  margin-top: 1.5rem;
}

.info-container-register option:hover {
  background-color: var(--azul-claro);
}

.register-birthday {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.birthday {
  margin-left: 3em;
  text-align: left;
  width: 50%;
  font-size: 1em;
  font-weight: 300;
}

.date-birthday {
  width: 100%;
}

.register-fac {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.register-date-fac {
  margin-left: 1.5em;
  text-align: left;
  width: 40%;
  font-size: 1em;
  font-weight: 300;
}

.date-birthday2 {
  width: 70%;
}

@media screen and (max-width: 1000px) {
  .container-register {
    width: 70%;
    margin-top: 3rem;
  }
  .info-container-register {
    width: 100%;
    border-radius: 5px;
  }
  .birthday {
    margin-left: 0;
  }
  .register-date-fac {
    margin-left: 0;
  }
  .date-birthday2 {
    width: 50%;
  }
}
@media screen and (max-width: 650px) {
  .container-register {
    width: 90%;
  }
}
@media (max-width: 500px) {
  .birthday {
    width: 60%;
  }
  .date-birthday {
    width: 82.5%;
  }
  .date-birthday2 {
    width: 65%;
  }
}